@tailwind base;
@tailwind components;
@tailwind utilities;


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* ================Loader============ */
.loading-container{
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.355);
  align-items: center;
  height: 100vh;
}

.loader{
  width: 50px;
  height: 50px;
  border: 2px solid #fff;
  border-radius: 50%;
  border-bottom-color: #ff3d00;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

.icon{
  width: 32px;
  height: 32px;
}
.icon1{
  width: 24px;

  display: inline-block;

}

