/* =============== GOOGLE FONTS =============== */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

/*=============== VARIABLES CSS ===============*/
:root {
  /*========== Colors ==========*/
  /*Color mode HSL(hue, saturation, lightness)*/
  --first-color: hsl(0, 93%, 58%);
  --second-color: hsl(1, 100%, 51%);
  --title-color: hsl(244, 12%, 12%);
  --text-color: hsl(255, 3%, 25%);
  --body-color: #fff;

  /*========== Font and typography ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --body-font: "Poppins", sans-serif;
  --h2-font-size: 1.25rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /*========== Font weight ==========*/
  --font-medium: 500;
  --font-semi-bold: 600;
}

/* Responsive typography */
@media screen and (min-width: 1024px) {
  :root {
    --h2-font-size: 1.75rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

/*=============== BASE ===============*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: var(--body-color);
  font-family: var(--body-font);
  color: var(--text-color);
  
}

input,
button {
  font-family: var(--body-font);
  /* border: none; */
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
  /* overflow-y: scroll; */
}




/*=============== LOGIN FORM ===============*/
.login_content,.login__form,.login__inputs{
  display: grid;
}
.login__form{
  margin-top: 80px;
  margin-bottom: 50px;
}

.login_content{
  position: relative;
  height: 100vh;
  align-items: center;
  /* overflow-y: auto; */
  
}
.register_content{
  display: grid;
  position: relative;
  height: auto;
  align-items: center;
  /* overflow-y: auto; */
  
}

.login__img{
  position: absolute;
  width: 100%;
  height: 100%;
  object-position: center;
  object-fit: cover;
  z-index: -1;

}

.login__form{
  background-color: hsla(244,16%,92%,0.6);
  border: 2px solid hsla(244,16%,92%,0.75);
  border-radius: 1rem;
  padding: 1.5rem;
  row-gap: 1.25rem;
  margin-inline: 1.5rem;
  backdrop-filter: blur(20px);

}

.login_title{
  color: var(--title-color);
  font-size: var(--h2-font-size);
  margin-bottom: 0.5rem;
}

.login_title span{
  color: var(--first-color);

}

.login__description{
  color: var(--text-color);
  font-size: var(--small-font-size);
}

.login__inputs{
  row-gap: .75rem;
  margin-bottom: 0.5rem;
}

.login_label{
  display: block;
  color: var(--title-color);
  font-size: var(--small-font-size);
  font-weight: var(--font-semi-bold);
  margin-bottom: 0.25rem;
}

.login_input{
  width: 100%;
  background: transparent;
  text-indent: 10px;
  padding: 14px 12px;
  border-radius: 7px;
  border: 2px solid var(--text-color);
  font-size: var(--small-font-size);
  font-weight: var(--font-medium);
  color: var(--title-color);
  transition: border 0.4s;
}

.login_input::placeholder{
  color: var(--text-color);

}
.login_input:focus,.login_input:valid{
  border: 2px solid var(--first-color);
}

.login__box{
  position: relative;
}

.login__box .login_input{
  padding-right: 36px;
}

.login__eye{
  width: max-content;
  height: max-content;
  position: absolute;
  top: 0;
  bottom: 0;
  right: .75rem;
  margin: auto 0;
  font-size: 1.25rem;
  cursor: pointer;
}

.login__check{
  display: flex;
  column-gap: .5rem;
  align-items: center;
}

.login__check-input{
  /* appearance: none; */
  height: 16px;
  width: 16px;
  border: 2px solid var(--text-color);
  background: transparent;
  border-radius: .25rem;
}

.login__check-label{
  font-size: var(--small-font-size);
}
.login__buttons{
  display: flex;
  column-gap: 0.75rem;
}
.login__button{
  width: 100%;
  padding: 14px 2rem;
  border-radius: 6px;
  background:linear-gradient(180deg,var(--first-color),var(--second-color));
  color: #fff;
  font-size: var(--small-font-size);
  font-weight: var(--font-semi-bold);
  box-shadow:0 6px 24px hsla(0, 83%, 60%, 0.5) ;
  margin-bottom: 1rem;
  cursor: pointer;
}

.last_button{
  background:#fff;
  border:2px solid var(--first-color) ;
  color: var(--first-color);
  box-shadow: none;
}

.login__forgot{
  text-decoration: none;
  font-size: var(--small-font-size);
  color: var(--first-color);

}
