/*=============== BREAKPOINTS ===============*/
/* For small devices */
@media screen and (max-width:360px){
  .login__buttons{
    flex-direction: column;
    row-gap: 1rem;
  }
  
}


/* For medium devices */
@media screen and (min-width:576px){
  .login__form{
    width: 500px;
    justify-self: center;
  }
}

/* For large devices */
@media screen and (min-width:1064px){
  
  .login__form{
    justify-self:flex-end;
    margin-right: 4.5rem;
  }
}